/* eslint-disable no-undef */
/* eslint-disable react/prop-types */
/* eslint-disable prettier/prettier */
/* eslint-disable react/no-unknown-property */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState, useRef } from "react";
import Loader from "../../loader/Loader";
import useLoader from "../../../utils/useLoader";
import { Button, Stack, Typography, styled } from '@mui/material';
import { sendFile } from "../../../services/SendFile";
import ConformationDialog from "../ConformationDialog/ConformationDialog";
import { IoIosAttach } from "react-icons/io";
import { ContactList } from "../../../services/ContactList";
import { UpdateChannel } from "../../../services/UpdateChannel";
import { CreateContact } from "../../../services/CreateContact";
import { GetmailSubject, GetmailBody, GetmailCc, GetmailTo } from "../../../utils/Outlookinfo";
import { SetmailSubject, SettoEmails, SetmailBody } from "../../../utils/Prepopulate";
import { ChannelsList } from "../../../services/ChannelList";
import { Token } from "../../../utils/accessToken";
import CryptoJS from "crypto-js";
import { useTranslation } from 'react-i18next';
import constants from "../../../utils/constants";
import { DeleteContact } from "../../../services/DeleteContact";
import FileUpload from "../FileUploader/Fileupload";
import Toast from "../Toast/Toast";

const VisuallyHiddenInput = styled('input')({
    clip: 'rect(0 0 0 0)',
    clipPath: 'inset(50%)',
    height: 1,
    overflow: 'hidden',
    position: 'absolute',
    bottom: 0,
    left: 0,
    whiteSpace: 'nowrap',
    width: 1,
});

const Conversation = ({ selectedChannel, channelsdata, onOkay, onSuccess, setResetAddIn }) => {
    const loading = useLoader();
    const { t, i18n } = useTranslation();
    const [isLoader, setIsLoader] = useState(loading);
    const [success, setSuccess] = useState(false); // State to store success message
    const [info, setInfo] = useState(false); // State to store info message
    const [error, setError] = useState(false); // State to store error message
    const [conformationDialog, setConformationDialog] = useState(false);
    const [open, setOpen] = useState(false);
    const [reloadAddIn, setReloadAddin] = useState(false);
    const [uploadFileId, setUploadFileId] = useState('');
    const [isSendButtonDisabled, setIsSendButtonDisabled] = useState(false);
    const [isnotowner, setisnotowner] = useState();
    const [editMessage, setEditMessage] = useState("");
    const [OkMessage, setOkMessage] = useState("");
    const [saveMessage, setSaveMessage] = useState("");
    const [contactEmail, setContactEmail] = useState("");
    const [RemovedEmail, setRemovedEmail] = useState("");
    const [removedEmailIDs, setRemovedEmailIDs] = useState("");
    const [uploadFile, setUploadFile] = useState("");
    const [uploadFileName, setUploadFileName] = useState("");
    const [newSubject, setNewSubject] = useState("");
    const [dynamicFileNo, setDynamicFileNo] = useState(0);
    const [uploadFileSize, setUploadFileSize] = useState(0);
    const [dynamicPercentage, setDynamicPercentage] = useState(0);
    const [fileNames, setFileNames] = useState([]);
    const [selectedFile, setSelectedFile] = useState([]);
    const [isUploading, setIsUploading] = useState(false);
    const [channelName, setchannelName] = useState();
    const [previousChannels, setPreviousChannels] = useState([]);
    const { decrypted_access_token } = Token();
    const [accessToken, setAccessToken] = useState(decrypted_access_token);
    const abortControllerRef = useRef(new AbortController());
    const fileInputRef = useRef(null);
    const currentLanguage = i18n.language;
    const Key = constants.secretKey;

    const handleUploadFileId = (id) => {
        console.log(id);
        setUploadFileId(id);

        if (id) {
            setIsSendButtonDisabled(false); // Re-enable the send button
        }
    };

    const handleFileChange = async (event) => {
        setUploadFile(true);
        setIsSendButtonDisabled(true); // Disable the send button
        abortControllerRef.current = new AbortController(); // Create a new instance for each upload
        const selectedFile = event.target.files[0];
        console.log(selectedFile);
        setSelectedFile(selectedFile);

        if (selectedFile) {
            setReloadAddin(false);
            setDynamicFileNo(0);
            setDynamicPercentage(0);
        }

        const fileSize = selectedFile.size;
        const truncateFile = async (file, fileSize) => {
            return new Promise((resolve) => {
                const reader = new FileReader();

                reader.onload = function (event) {
                    const text = event.target.result;
                    const truncatedText = text.substring(0, fileSize);
                    const truncatedBlob = new Blob([truncatedText], { type: file.type });
                    const truncatedFile = new File([truncatedBlob], file.name, { type: file.type });
                    resolve(truncatedFile);
                };

                reader.readAsText(file);
            });
        };

        const modifiedFile = new File([selectedFile], selectedFile.name, { type: selectedFile.type });
        // console.log(modifiedFile.name);
        const newModifiedFile = await truncateFile(modifiedFile, fileSize);
        setUploadFileName(newModifiedFile.name);
        setFileNames((prevFileNames) => [...prevFileNames, newModifiedFile.name]);
        simulateFileUpload(newModifiedFile, selectedFile);
        setIsUploading(true);
    };

    const simulateFileUpload = (file, selectedFile) => {
        const totalSize = file.size;
        setUploadFileSize(file.size);
        let uploadedSize = 0;
        const incrementValue = 50000;

        let intervalDuration;
        if (totalSize < 1024) {
            intervalDuration = 60;
        } else {
            intervalDuration = 240;
        }

        setTimeout(() => {
            const interval = setInterval(() => {
                const progress = Math.round((uploadedSize / totalSize) * 100).toFixed();
                setDynamicPercentage(progress);

                if (uploadedSize >= totalSize) {
                    clearInterval(interval);
                    setDynamicPercentage(100);
                    setDynamicFileNo((prevCount) => prevCount + 1);
                    setIsSendButtonDisabled(false);
                    setIsUploading(false);
                } else {
                    uploadedSize += incrementValue;
                }
            }, intervalDuration);

            const handleCancelButton = () => {
                clearInterval(interval);

                setTimeout(() => {
                    fileInputRef.current.value = '';
                    setUploadFileName('');
                    setIsUploading(false);
                }, 5000);
            };

            return handleCancelButton;
        }, 2000);

        uploadedSize = 0;
    };

    const handleEdit = (message) => {
        setEditMessage(message);
        console.log("Edit message:", message);
        setIsLoader(false);
    };

    const handleOk = (message) => {
        setOkMessage(message);
        onOkay()
        console.log("Edit message:", message);
        setIsLoader(false);
    };

    const handleSave = async (message) => {
        setIsLoader(true);
        setError(null);
        setSuccess(null);
        setSaveMessage(message);
        setConformationDialog(false);
        setOpen(false);
        console.log("Edit message:", message);

        if (message) {
            const subject = await GetmailSubject();
            const body = await GetmailBody();
            const ccEmails = await GetmailCc();
            const toEmails = await GetmailTo();

            sendFile(accessToken, selectedFile, selectedChannel, channelsdata, toEmails, ccEmails, subject, body, uploadFileId, [], (error, channelData, fileSentMsg) => {
                if (error) {
                    console.log("callback", error);
                    setError(error);
                    setIsLoader(false);
                } else {
                    // Update channel name is subject is changed
                    if (selectedChannel && selectedChannel !== false && selectedChannel.name !== subject) {
                        const matchedChannel = channelsdata.find(channel => channel.name == subject);
                        if (matchedChannel) {
                            console.log("Subject already exists:", matchedChannel);
                        } else {
                            UpdateChannel(accessToken, selectedChannel, subject, async (error, data, channelData) => {
                                if (error) {
                                    console.log("Update ChannelEerror Callback", error);
                                    setIsLoader(false);
                                } else {
                                    console.log("Update Channel Success Callback", channelData);
                                    setSuccess(fileSentMsg);
                                    await SettoEmails(null); // This will clear the To field
                                    await SetmailSubject(null); // This will clear the Subject field
                                    await SetmailBody(null); // This will clear the mail body
                                    setResetAddIn(true);
                                    setReloadAddin(true);
                                    onSuccess();
                                    setSelectedFile(''); // Clear the selected file state
                                    setIsLoader(false);
                                }
                            });
                        }
                    }

                    ContactList(accessToken, selectedChannel, (error, contactListArray, data) => {
                        if (error) {
                            console.log("ContactList Error Callback", error);
                        } else {
                            console.log("ContactList Success Callback", contactListArray);
                            const dataArray = data.map(item => ({
                                email: item.user.email,
                                id: item.id,
                                fullObject: item
                            }));

                            // Convert emails to lowercase
                            const lowerCaseContactList = dataArray.map(item => item.email.toLowerCase());
                            const lowerCaseToEmail = toEmails.map(email => email.toLowerCase());

                            // Find new emails
                            const newEmail = lowerCaseToEmail.filter(email => !lowerCaseContactList.includes(email));

                            // Find removed emails and their IDs
                            const removedEmails = dataArray
                                .filter(item => !lowerCaseToEmail.includes(item.email.toLowerCase()))
                                .map(item => ({ email: item.email, id: item.id }));

                            // Separate the removed emails and their IDs
                            const removedEmailObjects = removedEmails.map(item => item.email);
                            const removedEmailIDs = removedEmails.map(item => item.id);

                            // Assuming setContactEmail is used to set the new emails
                            setContactEmail(newEmail);

                            // Set the removed emails and their IDs
                            //setRemovedEmails(removedEmailObjects);
                            setRemovedEmailIDs(removedEmailIDs);
                            console.log("id to delete", removedEmailIDs)

                            // You can also set the removed emails using a similar function if needed
                            setRemovedEmail(removedEmailObjects);

                            console.log("New emails:", newEmail);

                            if (removedEmailIDs.length > 0) {
                                removedEmailIDs.forEach((channelId, index) => {
                                    // Call DeleteContact function if user remove email from ToEmails
                                    DeleteContact(accessToken, selectedChannel, channelId, async (error, data) => {
                                        if (error) {
                                            // console.log(error);
                                            setError(error);
                                            setSuccess(fileSentMsg);
                                            await SettoEmails(null); // This will clear the To field
                                            await SetmailSubject(null); // This will clear the Subject field
                                            await SetmailBody(null); // This will clear the mail body
                                            setResetAddIn(true);
                                            setReloadAddin(true);
                                            setSelectedFile(''); // Clear the selected file state
                                            setIsLoader(false);
                                        } else {
                                            console.log("Contact Removed Successfully");
                                            setSuccess(fileSentMsg);
                                            await SettoEmails(null); // This will clear the To field
                                            await SetmailSubject(null); // This will clear the Subject field
                                            await SetmailBody(null); // This will clear the mail body
                                            setResetAddIn(true);
                                            setReloadAddin(true);
                                            setSelectedFile(''); // Clear the selected file state
                                            setIsLoader(false);
                                        }
                                    });
                                });
                            } else {
                                console.log("All contacts are already exists in TitanFile channel No need to remove");
                            }

                            if (newEmail.length > 0) {
                                newEmail.forEach((email, index) => {
                                    // Call CreateContact function for each email
                                    CreateContact(accessToken, selectedChannel, email, async (error, data) => {
                                        if (error) {
                                            console.log(error);
                                            setError(error);
                                            setSuccess(fileSentMsg);
                                            await SettoEmails(null); // This will clear the To field
                                            await SetmailSubject(null); // This will clear the Subject field
                                            await SetmailBody(null); // This will clear the mail body
                                            setResetAddIn(true);
                                            setReloadAddin(true);
                                            setSelectedFile(''); // Clear the selected file state
                                            setIsLoader(false);
                                        } else {
                                            console.log("New Contact Created Successfully");
                                            setSuccess(fileSentMsg);
                                            await SettoEmails(null); // This will clear the To field
                                            await SetmailSubject(null); // This will clear the Subject field
                                            await SetmailBody(null); // This will clear the mail body
                                            setResetAddIn(true);
                                            setReloadAddin(true);
                                            setSelectedFile(''); // Clear the selected file state
                                            setIsLoader(false);
                                        }
                                    });
                                });
                            } else {
                                console.log("All contacts are already exists in TitanFile channel No need to add");
                            }
                        };
                    });
                }
            });
        }
    };

    const sendFileToChannel = () => {
        setIsLoader(true);
        ChannelsList(accessToken, (error, data) => {
            if (error) {
                console.log("ChannelList Error Callback", error);
                setError(error);
                setIsLoader(false);
            } else {
                console.log("ChannelList Success Callback", data);
                setError(null);

                // Compare current channels with previous channels
                const channelsList = localStorage.getItem("channels_list");
                let decryptedChannelsData = null;
                if (channelsList) {
                    const bytes = CryptoJS.AES.decrypt(channelsList, Key);
                    decryptedChannelsData = JSON.parse(bytes.toString(CryptoJS.enc.Utf8)); // Ensure decrypted data is parsed to JSON
                }
                const isNewUpdate = JSON.stringify(data) !== JSON.stringify(decryptedChannelsData);

                // If there's a new update, show message
                if (isNewUpdate) {
                    console.log("New update found in TitanFile");
                    setInfo("New update found in TitanFile channels");
                    setIsLoader(false);
                } else {
                    console.log("No new update in TitanFile");
                    setInfo(null);
                    handleContinueAddIn();
                }

                // Update previous channels
                setPreviousChannels(data);
                const encryptedChannelsList = CryptoJS.AES.encrypt(JSON.stringify(data), Key).toString(); // Convert data to string before encryption
                localStorage.setItem("channels_list", encryptedChannelsList);
            }
        });
    };

    const handleContinueAddIn = async () => {
        try {
            setIsLoader(true);
            setError(null);
            setSuccess(null);
            const subject = await GetmailSubject();
            const body = await GetmailBody();
            const ccEmails = await GetmailCc();
            const toEmails = await GetmailTo();

            setNewSubject(subject);
            if (selectedChannel) {
                ContactList(accessToken, selectedChannel, (error, contactListArray) => {
                    if (error) {
                        console.log("ContactList Error Callback", error);
                    } else {
                        console.log("ContactList Success Callback", contactListArray);
                        const contactListEmails = contactListArray.map(item => item.email);

                        const emailsWithoutManagerOrOwner = contactListArray.filter(contact => {
                            return contact.sharing_Permissions !== "contributor" && contact.sharing_Permissions !== "viewer" && contact.sharing_Permissions !== " limited_viewer";
                        });
                        const contactListEmail = emailsWithoutManagerOrOwner.map(item => item.email);
                        console.log("Emails with 'channel_owner' or 'manager' permissions:", contactListEmail);

                        const lowerCaseContactList = contactListEmails.map(email => email.toLowerCase());
                        const lowerCaseToEmail = toEmails.map(email => email.toLowerCase());

                        // Check new email is exist in ToEmails or not
                        const newEmail = lowerCaseToEmail.filter(email => !lowerCaseContactList.includes(email));
                        console.log("New emails:", newEmail);

                        // Assuming setContactEmail is used to set the new emails
                        setContactEmail(newEmail);

                        // Find removed emails
                        const removedEmail = lowerCaseContactList.filter(email => !lowerCaseToEmail.includes(email));

                        // Compare removedEmail with contactListEmail
                        const emailsWithManagerOrOwner = contactListEmail.filter(email => {
                            return removedEmail.includes(email);
                        });
                        // console.log("Removed email matched with channel_owner or manager email", emailsWithManagerOrOwner)

                        // You can also set the removed emails using a similar function if needed
                        setRemovedEmail(removedEmail);

                        // Check subject line is matched with channel name or not
                        const matchedChannel = channelsdata.find(channel => channel.name === subject);
                        if (matchedChannel) {
                            console.log("Matched Channel Name: ", matchedChannel.name);
                            setNewSubject(null);
                        } else {
                            console.log("No matching channel found. Subject: ", subject);
                            setNewSubject(subject);
                        }
                        console.log("Owner Email", selectedChannel.owner.email)
                        var name = selectedChannel.name;
                        console.log(name);
                        setchannelName(name);

                        // If subject name or toEmails is changed then show dialog box for conformation 
                        if (newEmail.length > 0 || !matchedChannel || removedEmail.length > 0) {
                            var user_email = localStorage.getItem("owner_email");
                            let decrypted_email;
                            if (user_email) {
                                const bytes = CryptoJS.AES.decrypt(user_email, Key);
                                decrypted_email = bytes.toString(CryptoJS.enc.Utf8);
                            }

                            if (decrypted_email && user_email) {
                                if (contactListEmail == decrypted_email) {
                                    setisnotowner(false);
                                    setConformationDialog(true);
                                    setOpen(true);
                                    setInfo(null);
                                    setIsLoader(false);
                                } else {
                                    setisnotowner(true);
                                    setConformationDialog(true);
                                    setInfo(null);
                                    setOpen(true);
                                }
                            }
                        } else {
                            console.log("No new contacts or subject found in compose items");
                            sendFile(accessToken, selectedFile, selectedChannel, channelsdata, toEmails, ccEmails, subject, body, uploadFileId, [], async (error, channelData, fileSentMsg) => {
                                if (error) {
                                    console.log("callback", error);
                                    setError(error);
                                    setIsLoader(false);
                                } else {
                                    setSuccess(fileSentMsg);
                                    await SettoEmails(null); // This will clear the To field
                                    await SetmailSubject(null); // This will clear the Subject field
                                    await SetmailBody(null); // This will clear the mail body
                                    setResetAddIn(true);
                                    setReloadAddin(true);
                                    setInfo(null);
                                    setSelectedFile(''); // Clear the selected file state
                                    console.log("callback", channelData);
                                    setIsLoader(false);
                                }
                            });
                        }
                    };
                });
            } else {
                sendFile(accessToken, selectedFile, selectedChannel, channelsdata, toEmails, ccEmails, subject, body, uploadFileId, [], async (error, data, fileSentMsg) => {
                    if (error) {
                        console.log("callback", error);
                        setError(error);
                        setIsLoader(false);
                    } else {
                        setSuccess(fileSentMsg);
                        onSuccess();
                        await SettoEmails(null); // This will clear the To field
                        await SetmailSubject(null); // This will clear the Subject field
                        await SetmailBody(null); // This will clear the mail body
                        setResetAddIn(true);
                        setReloadAddin(true);
                        setInfo(null);
                        setSelectedFile(''); // Clear the selected file state
                        console.log("callback", data);
                        setIsLoader(false);
                    }
                });
            }
        } catch (error) {
            console.error("Error in send File To Channel:", error);
            setIsLoader(false);
        }
    }

    const handleCancelButton = () => {
        setUploadFile(false);
        setUploadFileName("");
        setDynamicPercentage(0);
        setDynamicFileNo(0);
        setIsUploading(false);
        setFileNames([]);
        abortControllerRef.current.abort();
        setIsSendButtonDisabled(false); // Re-enable the send button
    };

    return (
        <>
            {isLoader && (<Loader />)}
            <Stack
                sx={{
                    display: "flex",
                    flexDirection: "row",
                    marginTop: '2rem',
                    marginLeft: "1rem",
                    gap: "4px",
                    maxWidth: currentLanguage === 'fr' ? '100%' : '84%',
                }}
            >
                <Button
                    type="submit"
                    sx={{
                        backgroundImage: 'linear-gradient(to right, #41AD49, #78BB33)',
                        color: 'white',
                        textTransform: 'capitalize',
                        height: '40px',
                        paddingLeft: "2%",
                        paddingRight: "2%",
                        fontSize: currentLanguage === 'fr' ? '9px' : '12px',
                        transition: 'background-image 0.3s ease-in-out, transform 0.3s ease-in-out',
                        '&:hover': {
                            opacity: 0.9,
                        },

                        // For respoonsive button width
                        width: {
                            xs: '36%',
                            sm: '36%',
                            md: '26%',
                            lg: '40%'
                        }
                    }}
                    disabled={isSendButtonDisabled} // Disable the button based on the state
                    onClick={sendFileToChannel}
                >
                    {t('secure_send')}
                </Button>
                <Button
                    component="label"
                    variant="outlined"
                    sx={{
                        textTransform: 'capitalize',
                        color: "black",
                        borderColor: "gray",
                        paddingLeft: "2px",
                        paddingRight: "6px",
                        fontSize: currentLanguage === 'fr' ? '9px' : '12px',
                        transition: 'background-image 0.3s ease-in-out, transform 0.3s ease-in-out',
                        '&:hover': {
                            opacity: 0.9,
                            borderColor: "gray",
                            backgroundColor: "#f5f5f5"
                        },

                        // For respoonsive button width
                        width: {
                            xs: '56%',
                            sm: '56%',
                            md: '56%',
                            lg: '60%'
                        },
                    }}
                    startIcon={<IoIosAttach />}
                >
                    {t('attach_large_file')}
                    <VisuallyHiddenInput
                        type="file"
                        ref={fileInputRef}
                        onChange={handleFileChange}
                    />
                </Button>
            </Stack>

            {!reloadAddIn && uploadFileName ? (
                selectedFile.size <= 10485760 ? (
                    <Stack>
                        {isUploading && (
                            <Typography
                                sx={{
                                    color: "black",
                                    paddingTop: "1.5rem",
                                    textAlign: "center"
                                }}
                            >
                                Uploading File...
                            </Typography>
                        )}
                        <Stack>
                            <Typography
                                sx={{
                                    color: "black",
                                    paddingTop: "1.5rem",
                                    paddingBottom: "0.5rem",
                                    textAlign: "center"
                                }}
                            >
                                {dynamicFileNo} of {dynamicFileNo}
                            </Typography>
                            <Stack
                                sx={{
                                    width: "91%",
                                    marginLeft: "0.75rem",
                                    marginRight: "0.75rem"
                                }}
                            >
                                <Stack
                                    sx={{
                                        width: '100%',
                                        height: '1rem',
                                        backgroundColor: '#F0F0F0',
                                        borderRadius: '0.5rem'
                                    }}
                                >
                                    <Stack
                                        sx={{
                                            border: '1px solid #41AD49',
                                            borderRadius: '0.5rem',
                                            height: '1rem',
                                            background: 'linear-gradient(to right, #41AD49, #78BB33)'
                                        }}
                                        style={{ width: `${dynamicPercentage}%` }}
                                    ></Stack>
                                </Stack>
                            </Stack>
                            <Typography
                                sx={{
                                    color: "black",
                                    paddingTop: "0.5rem",
                                    textAlign: "center"
                                }}
                            >
                                {dynamicPercentage}%
                            </Typography>
                        </Stack>
                        {isUploading && (
                            <Stack
                                sx={{
                                    display: "flex",
                                    justifyContent: "center",
                                    alignItems: "center"
                                }}
                            >
                                <Button
                                    sx={{
                                        color: "black",
                                        borderColor: "gray",
                                        textTransform: 'capitalize',
                                        width: "30%",
                                        marginTop: "12px",
                                        '&:hover': {
                                            opacity: 0.9,
                                            borderColor: "gray",
                                            backgroundColor: "#f5f5f5"
                                        }
                                    }}
                                    variant="outlined"
                                    onClick={handleCancelButton}
                                >
                                    Cancel
                                </Button>
                            </Stack>
                        )}
                    </Stack>
                ) : (
                    <Stack>
                        {uploadFile && (
                            <FileUpload
                                selectedFile={selectedFile}
                                isUploading={isUploading}
                                onUploadComplete={handleUploadFileId}
                                abortController={abortControllerRef.current}
                            />
                        )}

                        {isUploading && (
                            <Stack
                                sx={{
                                    display: "flex",
                                    justifyContent: "center",
                                    alignItems: "center"
                                }}
                            >
                                <Button
                                    sx={{
                                        color: "black",
                                        borderColor: "gray",
                                        textTransform: 'capitalize',
                                        width: "30%",
                                        marginTop: "12px",
                                        '&:hover': {
                                            opacity: 0.9,
                                            borderColor: "gray",
                                            backgroundColor: "#f5f5f5"
                                        }
                                    }}
                                    variant="outlined"
                                    onClick={handleCancelButton}
                                >
                                    Cancel
                                </Button>
                            </Stack>
                        )}
                    </Stack>
                )
            ) : null}

            {conformationDialog &&
                <ConformationDialog
                    open={open}
                    setConformationDialog={setConformationDialog}
                    onEdit={handleEdit}
                    onSave={handleSave}
                    onOk={handleOk}
                    newEmail={contactEmail}
                    newSubject={newSubject}
                    isnotowner={isnotowner}
                    ChannelName={channelName}
                    EmailfroRemove={RemovedEmail}
                />
            }

            {/* Display success message if success state is not null else show error */}
            {success && error ? (
                <Toast success={success} error={error} />
            ) : info ? (
                <Toast info={info} handleContinueAddIn={handleContinueAddIn} />
            ) : success ? (
                <Toast success={success} />
            ) : error ? (
                <Toast error={error} />
            ) : null}
        </>
    );
};

export default Conversation;
