/* eslint-disable react/prop-types */
/* eslint-disable prettier/prettier */
import React from 'react'
import {
    Box,
    CircularProgress,
    circularProgressClasses
} from '@mui/material';

const Loader = () => {
    return (
        <>
            <div style={{
                position: 'fixed',
                top: 0,
                left: 0,
                width: '100%',
                height: '100%',
                backgroundColor: 'rgba(255, 255, 255, 0.8)',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                zIndex: 1000,
            }}
            >
                <Box sx={{ position: "relative" }}>
                    <CircularProgress
                        variant="determinate"
                        sx={{
                            color: (theme) =>
                                theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800],
                        }}
                        style={{ size: '40px' }}
                        thickness={4}
                        value={100}
                    />
                    <CircularProgress
                        variant="indeterminate"
                        disableShrink
                        sx={{
                            color: 'transparent',
                            animationDuration: '550ms',
                            position: 'absolute',
                            left: 0,
                            [`& .${circularProgressClasses.circle}`]: {
                                strokeLinecap: 'round',
                                stroke: 'url(#linearColors)',
                            },
                        }}
                        style={{ size: '40px' }}
                        thickness={4}
                    />
                    <svg width="0" height="0">
                        <defs>
                            <linearGradient id="linearColors" x1="1" y1="0" x2="0" y2="1">
                                <stop offset="0%" stopColor="#41AD49" />
                                <stop offset="100%" stopColor="#78BB33" />
                            </linearGradient>
                        </defs>
                    </svg>
                </Box>
            </div>
        </>
    )
}

export default Loader;
