/* eslint-disable no-undef */
/* eslint-disable prettier/prettier */
import constants from "../utils/constants";
import CryptoJS from "crypto-js";

export const AuthFunction = async (code, code_verifier, callback) => {
    if (code && code_verifier) {
        const myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/x-www-form-urlencoded");
        myHeaders.append("Cookie", `sessionid=${constants.session_id}`);

        const urlencoded = new URLSearchParams();
        urlencoded.append("client_id", constants.client_id);
        urlencoded.append("code", code);
        urlencoded.append("code_verifier", code_verifier);
        urlencoded.append("redirect_uri", constants.redirect_uri);
        urlencoded.append("grant_type", constants.grant_type);

        const requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: urlencoded,
            redirect: "follow"
        };

        fetch(`${constants.BASE_URL}/api/v1/oauth/token/`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                console.log(result);
                let access_token = result.access_token;
                let refresh_token = result.refresh_token;
                let expires_in = result.expires_in;
                let expires_in_string;
                if (expires_in) {
                    expires_in_string = expires_in.toString();
                    console.log(expires_in_string);
                }

                if (access_token && refresh_token && expires_in) {
                    // encrypt all values
                    const encrypted_access_token = CryptoJS.AES.encrypt(access_token, constants.secretKey).toString();
                    const encrypted_refresh_token = CryptoJS.AES.encrypt(refresh_token, constants.secretKey).toString();
                    const encrypted_expires_in = CryptoJS.AES.encrypt(expires_in_string, constants.secretKey).toString();
                    const owner_email = CryptoJS.AES.encrypt(constants.email, constants.secretKey).toString();

                    // store all values in localstorage
                    localStorage.setItem("titanfile_access", encrypted_access_token);
                    localStorage.setItem("owner_email", owner_email);
                    localStorage.setItem("titanfile_refresh", encrypted_refresh_token);
                    localStorage.setItem("expires_in", encrypted_expires_in);

                    callback(null, access_token);
                } else {
                    callback(result, null);
                }
            })
            .catch((error) => {
                console.log(error);
                callback(error);
            });
    } else {
        console.log("Code not found");
    }
};
