/* eslint-disable no-undef */
/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
import constants from "../utils/constants";
import { CreateNewChannel } from "./NewChannel";
import { CreateContact } from "./CreateContact";
import { ContactList } from "./ContactList";

export const sendFile = (accessToken, selectedFile, selectedChannel, channelsdata, toEmails, ccEmails, subject, body, uploadFileId, newEmail, callback) => {
    let selectedChannelId;
    let selectedChannelEmail;
    let fileSentMsg;
    console.log("Upload File Id: ", uploadFileId);

    const Send = (selectedChannelId, selectedChannelEmail) => {
        if (accessToken) {
            const myHeaders = new Headers();
            myHeaders.append("Accept", `application/json; ${constants.version}`);
            myHeaders.append("Authorization", `Bearer ${accessToken}`);
            myHeaders.append("Cookie", `sessionid=${constants.session_id}`);

            const formdata = new FormData();
            formdata.append("channel", selectedChannelId);
            formdata.append("owner_email", selectedChannelEmail);
            formdata.append("message_html", body);

            if (selectedFile.size <= 10485760) {
                formdata.append("files", selectedFile);
            } else {
                formdata.append("uploadedFiles", uploadFileId);
            }

            const requestOptions = {
                method: "POST",
                headers: myHeaders,
                body: formdata,
                redirect: "follow"
            };

            fetch(`${constants.BASE_URL}/api/v1/messages/`, requestOptions)
                .then((response) => response.json())
                .then((result) => {
                    console.log(result);
                    if (result.id) {
                        if (selectedChannel) {
                            fileSentMsg = `Message send successfully to ${selectedChannel.name} channel`;
                        } else {
                            fileSentMsg = "Message send successfully to TitanFile channel";
                        }
                        callback(null, channelsdata, fileSentMsg);
                    } else {
                        // console.log(result.error);
                        callback(result.error.message);
                    }
                })
                .catch((error) => {
                    // console.error(error);
                    console.log(error.message);
                    callback(error.message);
                });
        } else {
            console.log("Missing a JWT Toekn");
        }
    };

    if (!selectedChannel || selectedChannel == false) {
        const matchedChannel = channelsdata.find(channel => channel.name == subject);
        if (matchedChannel) {
            console.log("Subject already exists:", matchedChannel);
            var Subject = subject + " 1";
            CreateNewChannel(accessToken, Subject, channelsdata, (error, data, channelData) => {
                if (error) {
                    console.log(error);
                    callback(error, null);
                } else {
                    const selectedChannel = data;
                    selectedChannelId = data.id;
                    selectedChannelEmail = data.owner.email;
                    console.log("new channel created", channelData);
                    callback(null, channelData);

                    if (toEmails) {
                        ContactList(accessToken, selectedChannel, (error, contactListArray) => {
                            if (error) {
                                console.log("ContactList Callback", error);
                            } else {
                                console.log("ContactList Callback", contactListArray);
                                const contactListEmails = contactListArray.map(item => item.email);

                                const lowerCaseContactList = contactListEmails.map(email => email.toLowerCase());
                                const lowerCaseToEmail = toEmails.map(email => email.toLowerCase());
                                const newEmail = lowerCaseToEmail.filter(email => !lowerCaseContactList.includes(email));
                                console.log("New emails:", newEmail);

                                if (toEmails.length > 0) {
                                    toEmails.forEach((email, index) => {
                                        CreateContact(accessToken, selectedChannel, email, (error, data) => {
                                            if (error) {
                                                console.log(error);
                                                callback(error, null);
                                            } else {
                                                console.log("New Contact Created:", data);
                                                Send(selectedChannelId, selectedChannelEmail);
                                            }
                                        });
                                    })
                                } else {
                                    console.log("No contact email found in ToEmails");
                                }
                            };
                        });
                    } else {
                        Send(selectedChannelId, selectedChannelEmail);
                    }

                    //setLoadingapi(false);
                    console.log("callback", channelData);
                }
            });
        } else {
            console.log("Subject: ", subject);
            CreateNewChannel(accessToken, subject, channelsdata, async (error, data, channelData) => {
                if (error) {
                    console.log(error);
                } else {
                    const selectedChannel = data;
                    selectedChannelId = data.id;
                    selectedChannelEmail = data.owner.email;
                    console.log("new channel created", channelData)

                    if (toEmails.length > 0) {
                        ContactList(accessToken, selectedChannel, (error, contactListArray) => {
                            if (error) {
                                console.log("ContactList Callback", error);
                            } else {
                                console.log("ContactList Callback", contactListArray);

                                if (toEmails && toEmails.length > 0) {
                                    toEmails.forEach((email, index) => {
                                        CreateContact(accessToken, selectedChannel, email, async (error, data) => {
                                            if (error) {
                                                console.log(error);
                                                callback(error, null);
                                            } else {
                                                Send(selectedChannelId, selectedChannelEmail);
                                                console.log("New Contact Created", data);
                                            }
                                        });
                                    });
                                } else {
                                    console.log("All contacts are already exist in TitanFile channel");
                                }
                            };
                        });
                    } else {
                        Send(selectedChannelId, selectedChannelEmail);
                        console.log("No contact found in toEmails");
                    };
                }
            });
        }
    } else {
        if (newEmail.length > 0) {
            CreateContact(accessToken, selectedChannel, newEmail, async (error, data) => {
                if (error) {
                    console.log(error);
                    callback(error, null);
                } else {
                    console.log("New Contact Created Successfully");
                    selectedChannelId = selectedChannel.id;
                    selectedChannelEmail = selectedChannel.owner.email;
                    Send(selectedChannelId, selectedChannelEmail);
                }
            });
        } else {
            console.log("Contact already exist in channel");
            selectedChannelId = selectedChannel.id;
            selectedChannelEmail = selectedChannel.owner.email;
            Send(selectedChannelId, selectedChannelEmail);
        }
    }
};
