/* eslint-disable no-undef */
/* eslint-disable react/prop-types */
/* eslint-disable prettier/prettier */
import React, { useState, useEffect } from 'react';
import Loader from '../../loader/Loader';
import useLoader from '../../../utils/useLoader';
import { Dialog, DialogTitle, DialogContent, DialogActions, Divider, TextField, Button, Slide, styled, IconButton } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import constants from '../../../utils/constants';
import CryptoJS from 'crypto-js';
import { useTranslation } from 'react-i18next';
import { Token } from '../../../utils/accessToken';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="down" ref={ref} {...props} />;
});

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
    '& .MuiDialogContent-root': {
        padding: theme.spacing(2),
    },
    '& .MuiDialogActions-root': {
        padding: theme.spacing(1),
    },
}));

const NewChannel = ({ open, handleClose, setSuccess, setError, onSuccess }) => {
    const loading = useLoader();
    const { t } = useTranslation();
    const [isLoader, setIsLoader] = useState(loading);
    const [channelName, setChannelName] = useState('');
    const [creatingChannelDisabled, setCreatingChannelDisabled] = useState(false);
    const { decrypted_access_token } = Token();

    const Key = constants.secretKey
    let encryptedOwner_email = localStorage.getItem("owner_email");

    let decryptedOwner_email;
    if (encryptedOwner_email) {
        const bytes = CryptoJS.AES.decrypt(encryptedOwner_email, Key);
        decryptedOwner_email = bytes.toString(CryptoJS.enc.Utf8);
        // console.log(decryptedOwner_email);
    } else {
        decryptedOwner_email = null;
    }

    const handleCreateChannelClcik = () => {
        setIsLoader(true);
        setCreatingChannelDisabled(true);

        const myHeaders = new Headers();
        myHeaders.append("Accept", `application/json; ${constants.version}`);
        myHeaders.append("Authorization", `Bearer ${decrypted_access_token}`);
        myHeaders.append("Cookie", `sessionid=${constants.session_id}`);

        const formdata = new FormData();
        formdata.append("name", channelName);
        formdata.append("owner_email", decryptedOwner_email);

        const requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: formdata,
            redirect: "follow"
        };

        fetch(`${constants.BASE_URL}/api/v1/channels/`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                const data = result;

                if (data.id) {
                    setCreatingChannelDisabled(false);
                    setSuccess("New Channel created successfully");
                    setError(null);
                } else if (data.error) {
                    console.log(data.error.message);
                    setError(data.error.message);
                    setSuccess(null);
                }
                else {
                    console.log("Channel not created");
                }
                setIsLoader(false);
                onSuccess();
                handleClose();
            })
            .catch((error) => {
                console.error(error);
                setCreatingChannelDisabled(false);
            });
    }

    useEffect(() => {
        setCreatingChannelDisabled(!channelName);
    }, [channelName]);

    return (
        <>
            <BootstrapDialog
                onClose={handleClose}
                aria-labelledby="customized-dialog-title"
                open={open}
                TransitionComponent={Transition}
                sx={{
                    '& .MuiDialog-paper': {
                        width: '600px',
                        height: '200px',
                    },
                }}
            >
                {isLoader && (<Loader />)}

                <DialogTitle sx={{ m: 0, p: 1, paddingLeft: "1rem", fontWeight: "bold" }} id="customized-dialog-title">
                    {t('new_channel')}
                </DialogTitle>
                <IconButton
                    aria-label="close"
                    onClick={handleClose}
                    sx={{
                        position: 'absolute',
                        right: 8,
                        top: 4,
                        color: (theme) => theme.palette.grey[500],
                    }}
                >
                    <CloseIcon sx={{ color: "black" }} />
                </IconButton>
                <Divider />
                <DialogContent>
                    <TextField
                        label={t('cancel_text')}
                        placeholder={t('channel_name')}
                        size="small"
                        sx={{ width: "100%" }}
                        value={channelName}
                        onChange={(e) => setChannelName(e.target.value)}
                    />
                </DialogContent>
                <DialogActions sx={{ marginTop: "4px", marginBottom: "1rem", display: "flex", justifyContent: "space-between" }}>
                    <Button
                        variant="outlined"
                        sx={{
                            color: "black",
                            border: "1px solid gray",
                            marginLeft: "0.5rem",
                            width: "46%",
                            '&:hover': {
                                opacity: 0.9,
                                border: "1px solid gray",
                                background: "gray-500"
                            }
                        }}
                        onClick={handleClose}
                    >
                        {t('cancel_text')}
                    </Button>

                    <Button
                        sx={{
                            backgroundImage: 'linear-gradient(to right, #41AD49, #78BB33)',
                            color: "white",
                            width: "46%",
                            marginLeft: "0.5rem",
                            marginRight: "0.5rem",
                            '&:hover': {
                                opacity: 0.9,
                            }
                        }}
                        disabled={creatingChannelDisabled}
                        onClick={handleCreateChannelClcik}
                    >
                        {t('save_text')}
                    </Button>
                </DialogActions>
            </BootstrapDialog>
        </>
    );
};

export default NewChannel;
