/* eslint-disable prettier/prettier */
export const SettoEmails = (selectedChannelContact) => {
    let toEmails = selectedChannelContact ? selectedChannelContact : "";
    console.log(toEmails);

    return new Promise((resolve, reject) => {
        // Clear the body if toEmails is null or an empty string
        if (toEmails === "") {
            Office.context.mailbox.item.to.setAsync([], function (asyncResult) { // setting to an empty array
                if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
                    // console.log(`Succeeded in clearing To field.`);
                    resolve();
                } else {
                    reject(asyncResult.error);
                }
            });
        } else {
            Office.context.mailbox.item.to.setAsync(toEmails, function (asyncResult) {
                if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
                    // console.log(`Succeeded in setting To field: ${toEmails}`);
                    resolve();
                } else {
                    reject(asyncResult.error);
                }
            });
        }
    });
};

export const SetccEmails = () => {
    const ccEmails = ["test@gmail.com"];
    return new Promise((resolve, reject) => {
        if (ccEmails) {
            Office.context.mailbox.item.cc.setAsync(ccEmails, function (asyncResult) {
                if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
                    // console.log(`Succeeded in setting Cc field: ${ccEmails}`);
                    resolve();
                } else {
                    reject(asyncResult.error);
                }
            });
        }
    });
};

export const SetmailSubject = (selectedChannelData) => {
    let mailSubject = selectedChannelData && selectedChannelData.name ? selectedChannelData.name : "";
    console.log(mailSubject);

    return new Promise((resolve, reject) => {
        // Clear the body if mailSubject is null or an empty string
        if (mailSubject === "") {
            Office.context.mailbox.item.subject.setAsync(mailSubject, (result) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    // console.log(`Successfully cleared subject.`);
                    resolve();
                } else {
                    reject(result.error);
                }
            });
        } else {
            Office.context.mailbox.item.subject.setAsync(mailSubject, (result) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    // console.log(`Successfully set subject to: ${mailSubject}`);
                    resolve();
                } else {
                    reject(result.error);
                }
            });
        }
    });
};

export const SetmailBody = (mailBody) => {
    let mailBodyText = mailBody ? mailBody : "";
    console.log(mailBodyText);

    return new Promise((resolve, reject) => {
        if (mailBodyText == "") {
            // Clear the body if mailBody is null or an empty string
            Office.context.mailbox.item.body.setAsync(mailBodyText, (result) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    // console.log(`Successfully cleared the body.`);
                    resolve();
                } else {
                    reject(result.error);
                }
            });
        } else {
            Office.context.mailbox.item.body.setAsync(mailBodyText, (result) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    // console.log(`Successfully set body to: ${mailBodyText}`);
                    resolve();
                } else {
                    reject(result.error);
                }
            });
        }
    });
};
