/* eslint-disable no-undef */
/* eslint-disable prettier/prettier */
import { useState, useEffect } from 'react';

export const PKCEGenerator = () => {
    const [code_verifier, setCodeVerifier] = useState('');
    const [code_challenge, setCodeChallenge] = useState('');

    const generateRandomString = (length) => {
        const possibleCharacters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        let randomString = '';
        for (let i = 0; i < length; i++) {
            randomString += possibleCharacters.charAt(Math.floor(Math.random() * possibleCharacters.length));
        }
        return randomString;
    };

    const base64UrlEncode = (str) => {
        return btoa(str)
            .replace(/\+/g, '-')
            .replace(/\//g, '_')
            .replace(/=+$/, '');
    };

    const sha256 = async (verifier) => {
        const encoder = new TextEncoder();
        const data = encoder.encode(verifier);
        const hash = await window.crypto.subtle.digest('SHA-256', data);
        return hash;
    };

    const generateCodeChallenge = async (verifier) => {
        const hash = await sha256(verifier);
        const base64Url = base64UrlEncode(String.fromCharCode(...new Uint8Array(hash)));
        return base64Url;
    };

    useEffect(() => {
        const generatePKCE = async () => {
            const verifier = generateRandomString(Math.floor(Math.random() * (128 - 43 + 1)) + 43);
            setCodeVerifier(verifier);
            const challenge = await generateCodeChallenge(verifier);
            setCodeChallenge(challenge);
        };

        generatePKCE();
    }, []);

    return { code_verifier, code_challenge };
};
