/* eslint-disable no-undef */
/* eslint-disable prettier/prettier */
import CryptoJS from "crypto-js";
import constants from "./constants";

export const Token = () => {
    const Key = constants.secretKey;
    let decrypted_access_token;
    let decrypted_expires_in;

    // Get encrypted values from localstorage
    const encrypted_access_token = localStorage.getItem("titanfile_access");
    const encrypted_expires_in = localStorage.getItem("expires_in");

    if (encrypted_access_token && encrypted_expires_in) {
        // decrypt access token
        const access_token_bytes = CryptoJS.AES.decrypt(encrypted_access_token, Key);
        decrypted_access_token = access_token_bytes.toString(CryptoJS.enc.Utf8);

        // decrypt expirey time
        const expires_in_bytes = CryptoJS.AES.decrypt(encrypted_expires_in, Key);
        decrypted_expires_in = expires_in_bytes.toString(CryptoJS.enc.Utf8);
    } else {
        decrypted_access_token = null;
        decrypted_expires_in = null;
    }

    return { decrypted_access_token, decrypted_expires_in };
};