/* eslint-disable prettier/prettier */
import CryptoJS from "crypto-js";
import constants from "./constants";

export const Refresh = () => {
    const Key = constants.secretKey;
    let decrypted_refresh_token;

    // Get encrypted value from localstorage
    const encrypted_refresh_token = localStorage.getItem("titanfile_refresh");

    if (encrypted_refresh_token) {
        // decrypt refresh token
        const bytes = CryptoJS.AES.decrypt(encrypted_refresh_token, Key);
        decrypted_refresh_token = bytes.toString(CryptoJS.enc.Utf8);
    } else {
        decrypted_refresh_token = null;
    }

    return decrypted_refresh_token;
};