/* eslint-disable no-undef */
/* eslint-disable prettier/prettier */
export const OAuthDialog = (authorizationUrl, callback) => {
    // Open Office dialog with the constructed authorization URL
    let dialog;
    Office.onReady(function (info) {
        // if (info.host === Office.HostType.Outlook) {
        if (Office.context && Office.context.ui) {
            Office.context.ui.displayDialogAsync(
                authorizationUrl,
                { height: 80, width: 40, requireHTTPS: true, promptBeforeOpen: false },
                function (asyncResult) {
                    if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
                        dialog = asyncResult.value;
                        dialog.addEventHandler(Office.EventType.DialogMessageReceived, function (arg) {
                            const code = arg.message;
                            // console.log('Message received from Office dialog:', code);
                            dialog.close();

                            // Invoke callback with the received code
                            callback(code);
                        });
                    } else {
                        console.error('Failed to open dialog:', asyncResult.error.message);
                    }
                }
            );
        } else {
            console.error('Office context or UI not available.');
        }
        // } else {
        //     console.error('Office host is not Outlook.');
        // }
    });
};
