/* eslint-disable prettier/prettier */
import constants from "../utils/constants";

export const ContactList = async (accessToken, selectedChannel, callback) => {
    const selectedChannelId = selectedChannel.id;

    if (accessToken && selectedChannel && selectedChannelId) {
        const myHeaders = new Headers();
        myHeaders.append("Accept", `application/json; ${constants.version}`);
        myHeaders.append("Authorization", `Bearer ${accessToken}`);
        myHeaders.append("Cookie", `sessionid=${constants.session_id}`);

        const requestOptions = {
            method: "GET",
            headers: myHeaders,
            redirect: "follow"
        };

        fetch(`${constants.BASE_URL}/api/v1/channel_contacts/?channel=${selectedChannelId}`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                // console.log(result);
                if (result.data) {
                    var data = result.data;

                    const contactListArray = data.map(item => ({
                        email: item.user.email,
                        sharing_Permissions: item.sharing_permissions
                    }));
                    callback(null, contactListArray, data);
                } else if (result.error) {
                    console.log(result.error.message);
                    callback(result.error.message, null);
                }
                else {
                    console.log("Can't Get channel_contacts");
                    callback("Can't Get channel_contacts", null);
                }
            })
            .catch((error) => {
                console.log(error);
            });
    }
};
