/* eslint-disable no-undef */
/* eslint-disable react/prop-types */
/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState, useEffect } from 'react';
import Loader from '../../loader/Loader';
import useLoader from '../../../utils/useLoader';
import { Stack, InputLabel, MenuItem, FormControl, Select, Box, Tooltip, Typography } from '@mui/material';
import { ChannelsList } from '../../../services/ChannelList';
import NewChannel from './NewChannel';
import Conversation from '../Conversation/Conversation';
import constants from '../../../utils/constants';
import { CiSquarePlus } from "react-icons/ci";
import { SetmailSubject, SettoEmails, SetmailBody } from '../../../utils/Prepopulate';
import { ContactList } from '../../../services/ContactList';
import { RefreshToken } from '../../../auth/refreshToken';
import { Token } from '../../../utils/accessToken';
import { Refresh } from '../../../utils/refreshToken';
import Header from '../Header/Header';
import Toast from '../Toast/Toast';
import { useTranslation } from 'react-i18next';
import CryptoJS from 'crypto-js';
import { useNavigate } from 'react-router-dom';

const Channels = () => {
    const loading = useLoader();
    const navigate = useNavigate();
    const { t, i18n } = useTranslation();
    const [isLoader, setIsLoader] = useState(loading);
    const [open, setOpen] = useState(false);
    const [resetAddIn, setResetAddIn] = useState(false);
    const [selectedValue, setSelectedValue] = useState("");
    const [error, setError] = useState(null); // State to store error message
    const [success, setSuccess] = useState(null); // State to store success message
    const [channels, setChannels] = useState([]);
    const [selectedChannel, setSelectedChannel] = useState(null);
    const [updateChannels, setUpdateChannels] = useState(false);
    const { decrypted_access_token } = Token();
    const decrypted_refresh_token = Refresh();
    const [accessToken, setAccessToken] = useState(decrypted_access_token);
    const [refreshToken, setRefreshToken] = useState(decrypted_refresh_token);
    const currentLanguage = i18n.language;
    const key = constants.secretKey;

    var user_email = localStorage.getItem("owner_email");
    let decrypted_email;
    if (user_email) {
        const bytes = CryptoJS.AES.decrypt(user_email, key);
        decrypted_email = bytes.toString(CryptoJS.enc.Utf8);
        // console.log(decrypted_email);
    }

    useEffect(() => {
        if (loading && accessToken) {
            setIsLoader(true);
        }
    }, [loading]);

    useEffect(() => {
        if (accessToken && refreshToken) {
            // Check RefreshToken is expire or not if RefreshToken is expire then call API
            RefreshToken(refreshToken, (error, newAccessToken) => {
                if (error) {
                    if (error !== "accessToken not expired yet") {
                        console.log("RefreshToken Error Callback", error.code);
                        setError(error.code);
                        setIsLoader(false);
                    } else {
                        console.log(error);
                        ChannelsList(accessToken, (error, data) => {
                            if (error) {
                                console.log("ChannelList Error Callback", error);
                                setError(error);
                                setIsLoader(false);
                            } else {
                                console.log("ChannelList Success Callback", data);
                                setError(null);
                                setChannels(data);
                                setIsLoader(false);
                            }
                        });
                    }
                } else {
                    console.log("RefreshToken Success Callback", newAccessToken);
                    ChannelsList(accessToken, (error, data) => {
                        if (error) {
                            console.log("ChannelList Error Callback", error);
                            setError(error);
                            setIsLoader(false);
                        } else {
                            console.log("ChannelList Success Callback", data);
                            setError(null);
                            setChannels(data);
                            setIsLoader(false);
                        }
                    });
                }
            });
        }
    }, [accessToken, refreshToken]);

    // Trigger the channel list update
    const handleNewChannelSuccess = () => {
        setUpdateChannels(true);
    };

    // Trigger the reset add-in
    useEffect(() => {
        if (resetAddIn) {
            setSelectedValue(""); // Empty selected value
            setResetAddIn(false);
        }
    }, [resetAddIn]);

    useEffect(() => {
        if (updateChannels) {
            ChannelsList(accessToken, (error, data) => {
                if (error) {
                    console.log("ChannelList Error Callback", error);
                    setError(error);
                    setIsLoader(false);
                } else {
                    console.log("ChannelList Success Callback", data);
                    setError(null);
                    setChannels(data);
                    setUpdateChannels(false);
                    setIsLoader(false);
                }
            });
        }
    }, [updateChannels]);

    const handleNewChannelClick = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleOnChange = async (selectedValue) => {
        console.log("handle on change called");
        setIsLoader(true);
        const selectedChannelData = channels.find(channel => channel.id === selectedValue);
        setSelectedChannel(selectedChannelData);
        console.log(selectedChannelData);

        console.log(selectedChannelData.owner.email);
        console.log(selectedChannelData.name);

        if (selectedChannelData) {
            try {
                await ContactList(accessToken, selectedChannelData, async (error, contactListArray) => {
                    if (error) {
                        console.log("callback", error);
                        setError(error);
                        setIsLoader(false);
                    } else {
                        console.log("callback", contactListArray);
                        const contactListEmails = contactListArray.map(item => item.email);
                        const emailsWithoutManagerOrOwner = contactListArray.filter(contact => {
                            return contact.sharing_Permissions !== "channel_owner" && contact.sharing_Permissions !== "manager";
                        });
                        const contactListEmail = emailsWithoutManagerOrOwner.map(item => item.email);
                        console.log("Emails without 'channel_owner' or 'manager' permissions:", contactListEmail);

                        await SettoEmails(contactListEmails);
                        await SetmailSubject(selectedChannelData);

                        const mailBody = "Test Message for TitanFile Channel";
                        await SetmailBody(mailBody);
                        setError(null);
                        setIsLoader(false);
                    }
                });
            } catch (error) {
                console.error(error);
                setIsLoader(false);
            }
        } else {
            console.log("Channel not selected");
            setIsLoader(false);
        }
    };

    const handleOnOkay = async () => {
        console.log("handle on change called")
        setIsLoader(true);
        const selectedChannelId = selectedChannel.id;
        console.log(selectedChannelId);
        const selectedChannelData = channels.find(channel => channel.id === selectedChannelId);
        setSelectedChannel(selectedChannelData);
        console.log(selectedChannelData);

        if (selectedChannelData) {
            try {
                await ContactList(accessToken, selectedChannelData, async (error, contactListArray) => {
                    if (error) {
                        console.log("callback", error);
                        setError(error);
                        setIsLoader(false);
                    } else {
                        console.log("callback", contactListArray);
                        const contactListEmails = contactListArray.map(item => item.email);
                        const emailsWithoutManagerOrOwner = contactListArray.filter(contact => {
                            return contact.sharing_Permissions !== "channel_owner" && contact.sharing_Permissions !== "manager";
                        });
                        const contactListEmail = emailsWithoutManagerOrOwner.map(item => item.email);
                        console.log("Emails without 'channel_owner' or 'manager' permissions:", contactListEmail);

                        await SettoEmails(contactListEmails);
                        await SetmailSubject(selectedChannelData);

                        const mailBody = "Test Message for TitanFile Channel";
                        await SetmailBody(mailBody);
                        setError(null);
                        setIsLoader(false);
                    }
                });

            } catch (error) {
                console.error(error);
                setIsLoader(false);
            }
        } else {
            console.log("Channel not selected");
            setIsLoader(false);
        }
    };

    useEffect(() => {
        const encryptedToken = localStorage.getItem("titanfile_access");
        let decryptedToken;
        if (encryptedToken) {
            const bytes = CryptoJS.AES.decrypt(encryptedToken, key);
            decryptedToken = bytes.toString(CryptoJS.enc.Utf8);
        } else {
            decryptedToken = null;
        }
        if (decryptedToken) {
            navigate("/channelList");
        } else {
            navigate("/");
        }
    }, []);

    return (
        <>
            {isLoader && (<Loader />)}
            <Header />
            <Stack sx={{ paddingLeft: "16px", paddingTop: "8px" }}>
                <Typography>{t('hello_text')}</Typography>
                <Typography>{decrypted_email}</Typography>
            </Stack>

            {channels && (
                <Stack
                    sx={{
                        width: "100%",
                        marginLeft: "8px",
                        marginRight: "8px",
                        display: "flex",
                        flexDirection: "row",
                        alignItems: "center",
                        justifyContent: "space-between"
                    }}
                >
                    <Stack sx={{ marginLeft: "0.5rem", marginRight: "1rem", marginTop: "2.2rem", flexGrow: 1 }}>
                        <Stack
                            sx={{
                                display: "grid",
                                gridTemplateRows: "repeat(1fr)",
                                justifyItems: "start",
                                rowGap: "2px",
                                maxWidth: "100%",
                            }}
                        >
                            <FormControl sx={{ width: "102%" }} size="small">
                                <InputLabel id="demo-select-small-label">{t('select_channel')}</InputLabel>
                                <Select
                                    labelId="demo-select-small-label"
                                    id="demo-select-small"
                                    value={selectedValue}
                                    label="Select channel"
                                    MenuProps={{
                                        PaperProps: {
                                            style: {
                                                maxHeight: 250,
                                            },
                                        },
                                    }}
                                    onChange={(event) => {
                                        const value = event.target.value;
                                        setSelectedValue(value);
                                        handleOnChange(value);
                                    }}
                                >
                                    {channels.map((channel) => (
                                        <MenuItem sx={{ minHeight: "40px" }} key={channel.id} value={channel.id}>
                                            {channel.name}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                        </Stack>
                    </Stack>

                    {/* ToolTip for "If no channel is selected a new channel will be created" */}
                    <Stack sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', cursor: 'pointer' }}>
                        <Tooltip title="If no channel is selected a new channel will be created" placement="left-start">
                            <Box style={{ marginRight: "20px", color: "gray", marginTop: "34px", fontSize: "40px", cursor: "pointer" }} onClick={handleNewChannelClick}>
                                <CiSquarePlus />
                            </Box>
                        </Tooltip>
                    </Stack>
                </Stack>
            )}

            <Conversation
                selectedChannel={selectedChannel}
                channelsdata={channels}
                onOkay={handleOnOkay}
                onSuccess={handleNewChannelSuccess}
                setResetAddIn={setResetAddIn}
            />

            <NewChannel
                open={open}
                handleClose={handleClose}
                setSuccess={setSuccess}
                setError={setError}
                onSuccess={handleNewChannelSuccess}
            />

            <Stack sx={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                <Typography
                    sx={{
                        display: "flex",
                        flexDirection: "row",
                        fontSize: currentLanguage === 'fr' ? '11px' : '13px',
                        position: "absolute",
                        bottom: "6px"
                    }}
                >
                    TitanFile
                    <Typography
                        component="a"
                        href="https://www.titanfile.com/terms-policies/terms-of-service/"
                        target="_blank"
                        sx={{
                            color: "#03b1fc",
                            cursor: "pointer",
                            fontSize: currentLanguage === 'fr' ? '12px' : '13px',
                            paddingLeft: "4px",
                            paddingRight: "4px",
                            textDecoration: "underline",
                            '&:hover': {
                                textDecoration: "none"
                            },
                        }}
                    >
                        {t('terms_of_services')}
                    </Typography>
                    {t('and_text')}
                    <Typography
                        component="a"
                        href="https://www.titanfile.com/terms-policies/privacy-policy/"
                        target="_blank"
                        sx={{
                            color: "#03b1fc",
                            cursor: "pointer",
                            fontSize: currentLanguage === 'fr' ? '12px' : '13px',
                            paddingLeft: "4px",
                            textDecoration: "underline",
                            '&:hover': {
                                textDecoration: "none"
                            },
                        }}
                    >
                        {t('privacy_policy')}
                    </Typography>
                </Typography>
            </Stack>

            {/* Display success message if success state is not null else show error */}
            {success && error ? (
                <Toast success={success} error={error} />
            ) : success ? (
                <Toast success={success} />
            ) : error ? (
                <Toast error={error} />
            ) : null}
        </>
    )
}

export default Channels;
