/* eslint-disable no-undef */
/* eslint-disable react/prop-types */
/* eslint-disable prettier/prettier */
import React, { useState, useEffect } from "react";
import { Button, Dialog, Stack, Slide, DialogTitle, DialogContent, DialogActions, IconButton, Typography } from '@mui/material';
import { styled } from '@mui/material/styles';
import { useTranslation } from 'react-i18next';
import CloseIcon from '@mui/icons-material/Close';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="down" ref={ref} {...props} />;
});

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
    '& .MuiDialogContent-root': {
        padding: theme.spacing(2),
    },
    '& .MuiDialogActions-root': {
        padding: theme.spacing(1),
    },
}));

const ConformationBox = ({ open, setConformationDialog, onEdit, onSave, onOk, newEmail, newSubject, isnotowner, ChannelName, EmailfroRemove }) => {
    const { t } = useTranslation();
    const [notOwner, setnotOwner] = useState(false);
    const [newTitle, setTitle] = useState('');

    useEffect(() => {
        console.log(newEmail, newSubject);
        let title;
        if (newEmail.length == 0 && newSubject) {
            title = `Changing the subject line will rename the channel name in TitanFile from <b>${ChannelName}</b> to <b>${newSubject}</b>. Would you like to continue to send or edit your subject line?`
        }

        if (newEmail.length > 0 && !newSubject) {
            title = `Adding user <b>${newEmail}</b> to the To field will also add <b>${newEmail}</b> as a contact to existing channel <b>${ChannelName}</b> in TitanFile.. Would you like to continue to send or edit your To field?`
        }

        if (newEmail.length > 0 && newSubject) {
            title = `Changing the subject line will rename the channel name in TitanFile from <b>${ChannelName}</b>  to <b>${newSubject}</b> \n Adding user <b>${newEmail}</b> to the To field will also add <b>${newEmail}</b> as a contact to existing channel <b>${ChannelName}</b>. Would you like to continue to send or edit your subject line & To field?`;
        }

        if (newEmail.length == 0 && EmailfroRemove.length > 0 && EmailfroRemove) {
            title = `Removing user <b>${EmailfroRemove}</b> from the To field will also remove <b>${EmailfroRemove}</b> as a contact from existing channel <b>${ChannelName}</b> in TitanFile. Would you like to continue to send or edit your To field?`;
        }

        if (newEmail.length == 0 && EmailfroRemove.length > 0 && EmailfroRemove && newSubject) {
            title = `Removing user <b>${EmailfroRemove}</b> from the To field will also remove <b>${EmailfroRemove}</b> as a contact from existing channel <b>${ChannelName}</b> in TitanFile. Would you like to continue to send or edit your To field? \n Changing the subject line will rename the channel name in TitanFile from <b>${ChannelName}</b> to <b>${newSubject}</b>. Would you like to continue to send or edit your subject line?`;
        }

        if (isnotowner === true && newEmail.length == 0 && EmailfroRemove) {
            setnotOwner(true);
            title = `Only a channel owner or manager can remove a new user to an existing channel.`;
        }

        if (isnotowner === true && newEmail.length == 0 && EmailfroRemove.length > 0 && newSubject) {
            setnotOwner(true);
            title = `Only a channel owner or manager can remove a new user or edit the subject line of an existing channel.`;
        }

        if (isnotowner === true && newEmail.length > 0 && !newSubject) {
            setnotOwner(true);
            title = "Only a channel owner or manager can add a new user to an existing channel.";
        };

        if (isnotowner === true && newEmail.length > 0 && newSubject) {
            setnotOwner(true);
            title = "Only a channel owner or manager can add a new user or edit the subject line of an existing channel.";
        };

        setTitle(title);
    }, [isnotowner]);

    console.log(newTitle);
    const handleSave = () => {
        const message = "Save message sent";
        setConformationDialog(false);
        onSave(message);
    };

    const handleEdit = () => {
        const message = "Edit message sent";
        setConformationDialog(false);
        onEdit(message);
    };

    const handleOk = () => {
        const message = "ok";
        setConformationDialog(false);
        onOk(message);
    };

    return (
        <>
            <Stack
                sx={{
                    position: 'fixed',
                    zIndex: 50,
                    top: 0,
                    right: 0,
                    bottom: 0,
                    left: 0,
                    backgroundColor: 'rgba(26, 32, 44, 0.6)',
                    overflowY: 'auto',
                    height: '100%',
                    width: '100%',
                    paddingLeft: '1rem',
                    paddingRight: '1rem',
                }}
            >
                <Stack
                    sx={{
                        position: 'relative',
                        marginTop: '20px',
                        marginLeft: 'auto',
                        marginRight: 'auto',
                        boxShadow: '0 10px 15px rgba(0, 0, 0, 0.1), 0 4px 6px rgba(0, 0, 0, 0.1)',
                        borderRadius: '0.375rem',
                        backgroundColor: 'white',
                        maxWidth: '28rem'
                    }}
                >
                    <BootstrapDialog
                        TransitionComponent={Transition}
                        aria-labelledby="customized-dialog-title"
                        open={open}
                    >
                        <DialogTitle sx={{ m: 0, p: 2, fontWeight: "bold" }} id="customized-dialog-title">
                            {t('update_channel')}
                        </DialogTitle>
                        <IconButton
                            aria-label="close"
                            sx={{
                                position: 'absolute',
                                right: 8,
                                top: 8,
                                color: (theme) => theme.palette.grey[500],
                            }}
                            onClick={handleEdit}
                        >
                            <CloseIcon />
                        </IconButton>
                        <DialogContent>
                            {newTitle && (
                                <Stack sx={{ textAlign: "center", fontWeight: "medium", marginRight: "1rem" }}>
                                    <Typography dangerouslySetInnerHTML={{ __html: newTitle }} />
                                </Stack>
                            )}
                        </DialogContent>
                        <DialogActions>

                            {/* Conditional rendering based on contactEmail and matchedChannel */}
                            {notOwner ? (
                                < Button onClick={handleOk} variant="contained">{t('ok_text')}</Button>
                            ) : (
                                <>
                                    <Button
                                        sx={{
                                            color: "black",
                                            borderColor: "gray",
                                            textTransform: 'capitalize',
                                            '&:hover': {
                                                opacity: 0.9,
                                                borderColor: "gray",
                                                backgroundColor: "#f5f5f5"
                                            }
                                        }}
                                        variant="outlined"
                                        onClick={handleEdit}
                                    >
                                        {t('edit_text')}
                                    </Button>
                                    <Button
                                        sx={{
                                            backgroundImage: 'linear-gradient(to right, #41AD49, #78BB33)',
                                            color: 'white',
                                            textTransform: 'capitalize',
                                            '&:hover': {
                                                opacity: 0.9,
                                            }
                                        }}
                                        onClick={handleSave}
                                    >
                                        {t('send_text')}
                                    </Button>
                                </>
                            )}
                        </DialogActions>
                    </BootstrapDialog>
                </Stack>
            </Stack >
        </>
    )
}

export default ConformationBox;
