/* eslint-disable no-undef */
/* eslint-disable react/prop-types */
/* eslint-disable prettier/prettier */
import React, { useState, useEffect } from 'react';
import { Snackbar, Alert, Slide, Button } from '@mui/material';

const Toast = ({ success, error, info, handleContinueAddIn }) => {
    const [showError, setShowError] = useState(false);
    const [showSuccess, setShowSuccess] = useState(false);
    const [showInfo, setShowInfo] = useState(false);

    useEffect(() => {
        if (error) {
            setShowError(true);
        } else {
            setShowError(false);
        }

        if (success) {
            setShowSuccess(true);
        } else {
            setShowSuccess(false);
        }

        if (info) {
            setShowInfo(true);
        } else {
            setShowInfo(false);
        }
    }, [error, success, info]);  // Update success, error & info to the dependency array

    // Hide toast on cancel icon
    const handleCloseToast = (type) => (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        if (type === 'error') {
            setShowError(false);
        } else if (type === 'success') {
            setShowSuccess(false);
        } else if (type === 'info') {
            setShowInfo(false);
        }
    };

    const handleReloadAddIn = () => {
        location.reload();
        setShowInfo(false);
    };

    return (
        <>
            {/* Display error message if error state is not null  */}
            {error && (
                <Slide direction="up" in={showError} mountOnEnter unmountOnExit>
                    <Snackbar
                        open={showError}
                        autoHideDuration={6000}
                        onClose={handleCloseToast('error')}
                        sx={{
                            marginBottom: success ? '80px' : '0',
                        }}
                    >
                        <Alert
                            onClose={handleCloseToast('error')}
                            severity="error"
                            variant="filled"
                            sx={{ width: '100%' }}
                        >
                            {error}
                        </Alert>
                    </Snackbar>
                </Slide>
            )}

            {/* Display success message if success state is not null  */}
            {success && (
                <Slide direction="up" in={showSuccess} mountOnEnter unmountOnExit>
                    <Snackbar
                        open={showSuccess}
                        autoHideDuration={6000}
                        onClose={handleCloseToast('success')}
                    >
                        <Alert
                            onClose={handleCloseToast('success')}
                            severity="success"
                            variant="filled"
                            sx={{ width: '100%' }}
                        >
                            {success}
                        </Alert>
                    </Snackbar>
                </Slide>
            )}

            {/* Display info message if info state is not null */}
            {info && (
                <Slide direction="up" in={showInfo} mountOnEnter unmountOnExit>
                    <Snackbar
                        open={showInfo}
                        autoHideDuration={6000}
                        onClose={handleCloseToast('info')}
                    >
                        <Alert
                            onClose={handleCloseToast('info')}
                            severity="info"
                            variant="filled"
                            sx={{ width: '100%' }}
                        >
                            {info}
                            <Button
                                color="inherit"
                                size="small"
                                sx={{
                                    textTransform: "capitalize"
                                }}
                                onClick={() => {
                                    handleContinueAddIn();
                                    handleCloseToast('info')();
                                }}
                            >
                                Continue
                            </Button>
                            <Button
                                color="inherit"
                                size="small"
                                sx={{
                                    textTransform: "capitalize"
                                }}
                                onClick={handleReloadAddIn}
                            >
                                Reload
                            </Button>
                        </Alert>
                    </Snackbar>
                </Slide>
            )}
        </>
    );
};

export default Toast;
