//import ComposeInfo from "../utils/userInfo";
import constants from "../utils/constants";

export const UpdateChannel = (accessToken, selectedChannel, subject, callback) => {
    let channelId = selectedChannel.id;

    if (channelId && subject) {
        const myHeaders = new Headers();
        myHeaders.append("Accept", `application/json; ${constants.version}`);
        myHeaders.append("Authorization", `Bearer ${accessToken}`);
        myHeaders.append("Cookie", `sessionid=${constants.session_id}`);

        const formdata = new FormData();
        formdata.append("name", subject);

        const requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: formdata,
            redirect: "follow"
        };

        fetch(`${constants.BASE_URL}/api/v1/channels/${channelId}/`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                // console.log(result);
                if (result.id) {
                    callback(null, result);
                    console.log("Channel updated", result);
                } else if (result.error) {
                    console.log(result.error.message);
                    callback(result.error.message, null);
                }
                else {
                    console.log("Channel not updated");
                    callback("Channel not updated", null);
                }
            })
            .catch((error) => {
                console.error(error);
                callback(error.message, null);
            });
    }
}