/* eslint-disable prettier/prettier */
import constants from "../utils/constants";

export const DeleteContact = async (accessToken, selectedChannel, removedEmailIDs, callback) => {
    if (accessToken && removedEmailIDs) {
        const myHeaders = new Headers();
        myHeaders.append("Accept", "application/json; version=1.0");
        myHeaders.append("Authorization", `Bearer ${accessToken}`);
        myHeaders.append("Cookie", "sessionid=dxmrzqyay1ot1k1tijit54nrg96f6o9o");

        const formdata = new FormData();

        const requestOptions = {
            method: "DELETE",
            headers: myHeaders,
            body: formdata,
            redirect: "follow"
        };

        fetch(`${constants.BASE_URL}/api/v1/channel_contacts/${removedEmailIDs}/`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                // console.log(result);
                if (result.id) {
                    callback(null, result);
                } else if (result.error) {
                    callback(result.error.message, null);
                } else {
                    callback("unable to delete contact", null)
                }
            })
            .catch((error) => {
                console.log(error);
                callback(error.message, null);
            });
    }
}
