/* eslint-disable react/prop-types */
/* eslint-disable prettier/prettier */
import constants from "../utils/constants";

export const ChannelsList = async (accessToken, callback) => {
    if (accessToken) {
        const myHeaders = new Headers();
        myHeaders.append("Accept", `application/json; ${constants.version}`);
        myHeaders.append("Authorization", `Bearer ${accessToken}`);
        myHeaders.append("Cookie", `sessionid=${constants.session_id}`);

        const requestOptions = {
            method: "GET",
            headers: myHeaders,
            redirect: "follow"
        };

        fetch(`${constants.BASE_URL}/api/v1/channels/?filter=active&limit=100`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                if (result.data) {
                    // console.log("Channels List data", result);
                    let data = result.data;
                    callback(null, data);
                } else {
                    callback(result.error.message);
                }
            })
            .catch((error) => {
                console.log(error);
                callback("403 Error");
                // callback(error, null);
            });
    }
};
