/* eslint-disable prettier/prettier */
import constants from "../utils/constants";

export const CreateContact = async (accessToken, selectedChannel, contactEmail, callback) => {
    const channelId = selectedChannel.id;

    if (channelId && contactEmail) {
        const myHeaders = new Headers();
        myHeaders.append("Accept", `application/json; ${constants.version}`);
        myHeaders.append("Authorization", `Bearer ${accessToken}`);
        myHeaders.append("Cookie", `sessionid=${constants.session_id}`);

        const formdata = new FormData();
        formdata.append("channel", channelId);
        formdata.append("email", contactEmail);

        const requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: formdata,
            redirect: "follow"
        };

        fetch(`${constants.BASE_URL}/api/v1/channel_contacts/`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                // console.log(result);
                if (result.id) {
                    callback(null, result);
                } else if (result.error) {
                    callback(result.error.message, null);
                } else {
                    callback("unable to add new contact in channel", null)
                }
            })
            .catch((error) => {
                console.log(error);
                callback(error.message, null);
            });
    }
}
