/* eslint-disable no-undef */
/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState, useEffect } from 'react';
import { AppBar, Box, Toolbar, Button, Stack, Typography, TextField } from '@mui/material';
import useLoader from '../../../utils/useLoader';
import Loader from '../../loader/Loader';
import constants from '../../../utils/constants';
import LanguageProvider from '../../../locales/LanguageProvider';
import { OAuthDialog } from '../../../utils/OAuthDialog';
import { AuthFunction } from '../../../auth/auth';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { PKCEGenerator } from '../../../utils/PKCEGenerator';
import CryptoJS from "crypto-js";
import Toast from '../Toast/Toast';

const Login = () => {
    const loading = useLoader();
    const navigate = useNavigate();
    const { t, i18n } = useTranslation();
    const [isLoader, setIsLoader] = useState(loading);
    const [error, setError] = useState(null); // State to store error message
    const [success, setSuccess] = useState(null); // State to store success message
    const [code, setCode] = useState(loading);
    const [sub_domain, setSubDomain] = useState(''); // State for the TextField value
    const [isLoginButtonDisabled, setIsLoginButtonDisabled] = useState(true); // State to disable login button initially
    const { code_challenge, code_verifier } = PKCEGenerator();
    const currentLanguage = i18n.language;
    const key = constants.secretKey;

    useEffect(() => {
        if (code_challenge && code_verifier) {
            console.log("code_challenge: ", code_challenge);
            console.log("code_verifier: ", code_verifier);
        }
    }, [code_challenge, code_verifier]);

    const handleUrlChange = (event) => {
        const { value } = event.target;
        setSubDomain(value);
        setIsLoginButtonDisabled(event.target.value.trim() === '');
    };

    const handleTitanFileLogin = () => {
        setIsLoader(true);
        setIsLoginButtonDisabled(true); // Disable the button after clearing

        // Client credentials
        const client_id = constants.client_id;
        const redirect_uri = constants.redirect_uri;
        const code_challenge_method = constants.code_challenge_method;

        const sanitizeUrl = (url) => {
            let sanitizedUrl = url.trim();

            // Add 'https://' if not present
            if (!sanitizedUrl.startsWith('https://')) {
                sanitizedUrl = 'https://' + sanitizedUrl;
            }

            // Remove trailing slashes or backslashes
            sanitizedUrl = sanitizedUrl.replace(/[/\\]+$/, '');

            // Add '.titanfile.com' if not present
            if (!sanitizedUrl.endsWith('.titanfile.com')) {
                sanitizedUrl += '.titanfile.com';
            }

            return sanitizedUrl;
        };

        const isValidSubDomain = (url) => {
            const sanitizedUrl = sanitizeUrl(url);
            const containsUeStaging01 = sanitizedUrl.includes('ue-staging01');
            const startsWithHttps = sanitizedUrl.startsWith('https://');
            const endsWithTitanFile = sanitizedUrl.endsWith('.titanfile.com');

            return startsWithHttps && containsUeStaging01 && endsWithTitanFile;
        };

        const showError = (message) => {
            setError(null); // Reset error state
            setTimeout(() => setError(message), 0); // Set error state with a delay to ensure the state update
        };

        const sanitizedSubDomain = sanitizeUrl(sub_domain);

        if (isValidSubDomain(sanitizedSubDomain)) {
            const authorizationUrl = `${sanitizedSubDomain}/api/v1/oauth/authorize?response_type=code&client_id=${client_id}&redirect_uri=${redirect_uri}&code_challenge=${code_challenge}&code_challenge_method=${code_challenge_method}`;

            if (authorizationUrl) {
                OAuthDialog(authorizationUrl, handleOAuthCode);
            }
        } else {
            showError("Please enter a valid server domain.");
            setIsLoader(false);
        }
    };

    const handleOAuthCode = (code) => {
        //const removeQuotes = code.slice(1, -1);
        console.log('Received code:', code);
        setSubDomain(''); // Clear the input field after login
        setCode(code);
    };

    useEffect(() => {
        if (code && code_verifier) {
            AuthFunction(code, code_verifier, (error, data) => {
                if (error) {
                    console.log("AuthFunction Error Callback", error);
                    setError(error.error.code);
                    setCode(''); // Clear the code after processing the error
                    setIsLoader(false);
                } else {
                    console.log("AuthFunction Success Callback", data);
                    setError(null);
                    navigate("/channelList"); // Navigate to channelList component
                    setCode(''); // Clear the code after successful authentication

                    const message = {
                        type: Office.MailboxEnums.ItemNotificationMessageType.InformationalMessage,
                        message: "You are logged in to TitanFile. Use the Secure Send button in the side panel to send messages and files to TitanFile.",
                        icon: "icon80",
                        persistent: true,
                    };

                    // Show a notification message
                    Office.context.mailbox.item.notificationMessages.replaceAsync("action", message);
                    setIsLoader(false);
                }
            });
        }
    }, [code]); // Dependency array ensures this effect runs whenever 'code' changes

    useEffect(() => {
        const encrypted_token = localStorage.getItem("titanfile_access");
        let decrypted_token;
        if (encrypted_token) {
            const bytes = CryptoJS.AES.decrypt(encrypted_token, key);
            decrypted_token = bytes.toString(CryptoJS.enc.Utf8);
        } else {
            decrypted_token = null;
        }
        if (decrypted_token) {
            navigate("/channelList");
        } else {
            navigate("/");
        }
    }, []);

    const openOfficeDialog = () => {
        let dialog;
        const url = "https://junaid042.github.io/TitanFile/assets/redirect.html";
        Office.context.ui.displayDialogAsync(url, { height: 30, width: 20 },
            function (asyncResult) {
                dialog = asyncResult.value;
                dialog.addEventHandler(Office.EventType.DialogMessageReceived, function (arg) {
                    const code = arg.message;
                    console.log('Message received from Office dialog:', code);
                    dialog.close();
                });
            }
        );
    }

    return (
        <>
            {isLoader && (<Loader />)}
            <Stack>
                <Box sx={{ flexGrow: 1 }}>
                    <AppBar
                        sx={{
                            backgroundColor: 'white',
                            color: 'black',
                            boxShadow: '0 0 white'
                        }}
                        position="static"
                    >
                        <Toolbar sx={{ minHeight: "46px", display: "flex", justifyContent: "space-between" }}>
                            <Stack>
                                <img style={{ height: "20px" }} src="../assets/titanfilelogin.svg" alt="TitanFile" />
                            </Stack>
                            <LanguageProvider />
                        </Toolbar>
                    </AppBar>
                </Box>

                <Stack sx={{ flexGrow: 1 }}>
                    <Typography
                        sx={{
                            fontSize: currentLanguage === 'fr' ? '14px' : '15px',
                            paddingTop: "16px",
                            paddingLeft: "15px",
                            paddingBottom: "6px"
                        }}
                    >
                        {t('login_text_1')}
                    </Typography>

                    <Box
                        component="form"
                        sx={{
                            '& .MuiTextField-root': { width: '91.5%', marginLeft: "14px" },
                        }}
                        noValidate
                        autoComplete="off"
                    >
                        <TextField
                            size="small"
                            id="outlined-size-normal"
                            placeholder="https://"
                            value={sub_domain}
                            onChange={handleUrlChange}
                        />
                    </Box>

                    <Typography
                        sx={{
                            paddingTop: "6px",
                            paddingLeft: "15px",
                            fontSize: currentLanguage === 'fr' ? '14px' : '15px',
                        }}
                    >
                        {t('login_text_2')}
                    </Typography>
                </Stack>

                <Stack
                    sx={{
                        display: "flex",
                        justifyContent: "end",
                        alignItems: "end",
                        marginTop: "12px",
                        marginRight: "16px"
                    }}
                >
                    <Button
                        type="submit"
                        sx={{
                            backgroundImage: 'linear-gradient(to right, #41AD49, #78BB33)',
                            color: 'white',
                            textTransform: 'capitalize',
                            height: '40px',
                            fontSize: "12px",
                            maxWidth: "30%",
                            transition: 'background-image 0.3s ease-in-out, transform 0.3s ease-in-out',
                            '&:hover': {
                                opacity: 0.9,
                            },
                        }}
                        disabled={isLoginButtonDisabled} // Disable the button based on the state
                        onClick={handleTitanFileLogin}
                    >
                        {t('login_button')}
                    </Button>
                </Stack>

                {/* <Button variant="outlined" onClick={openOfficeDialog}>Open Office Dialog</Button> */}

                <Stack sx={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <Typography
                        sx={{
                            display: "flex",
                            flexDirection: "row",
                            fontSize: currentLanguage === 'fr' ? '11px' : '13px',
                            position: "absolute",
                            bottom: "6px"
                        }}
                    >
                        TitanFile
                        <Typography
                            component="a"
                            href="https://www.titanfile.com/terms-policies/terms-of-service/"
                            target="_blank"
                            sx={{
                                color: "#03b1fc",
                                cursor: "pointer",
                                fontSize: currentLanguage === 'fr' ? '12px' : '13px',
                                paddingLeft: "4px",
                                paddingRight: "4px",
                                textDecoration: "underline",
                                '&:hover': {
                                    textDecoration: "none"
                                },
                            }}
                        >
                            {t('terms_of_services')}
                        </Typography>
                        {t('and_text')}
                        <Typography
                            component="a"
                            href="https://www.titanfile.com/terms-policies/privacy-policy/"
                            target="_blank"
                            sx={{
                                color: "#03b1fc",
                                cursor: "pointer",
                                fontSize: currentLanguage === 'fr' ? '12px' : '13px',
                                paddingLeft: "4px",
                                textDecoration: "underline",
                                '&:hover': {
                                    textDecoration: "none"
                                },
                            }}
                        >
                            {t('privacy_policy')}
                        </Typography>
                    </Typography>
                </Stack>
            </Stack>

            {/* Display success message if success state is not null else show error */}
            {success && error ? (
                <Toast success={success} error={error} />
            ) : success ? (
                <Toast success={success} />
            ) : error ? (
                <Toast error={error} />
            ) : null}
        </>
    );
};

export default Login;
