/* eslint-disable no-undef */
/* eslint-disable prettier/prettier */
import constants from '../utils/constants';
import CryptoJS from "crypto-js";

export const RefreshToken = async (refreshToken, callback) => {
    const Key = constants.secretKey;
    let encrypted_expires_in = localStorage.getItem("expires_in");

    let decrypted_expires_in;
    let expires_in;
    if (encrypted_expires_in) {
        const bytes = CryptoJS.AES.decrypt(encrypted_expires_in, Key);
        decrypted_expires_in = bytes.toString(CryptoJS.enc.Utf8);

        // Convert the decrypted string back to an integer
        expires_in = parseInt(decrypted_expires_in, 10);
        console.log(expires_in * 1000)
        console.log(expires_in);
    } else {
        decrypted_expires_in = null;
    }

    if (refreshToken && expires_in * 1000 > Date.now() + 3600000) {
        const myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/x-www-form-urlencoded");
        myHeaders.append("Cookie", `sessionid=${constants.session_id}`);

        const urlencoded = new URLSearchParams();
        urlencoded.append("client_id", constants.client_id);
        urlencoded.append("client_secret", '');
        urlencoded.append("grant_type", constants.refresh_token_grant_type);
        urlencoded.append("refresh_token", refreshToken);

        const requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: urlencoded,
            redirect: "follow"
        };

        fetch(`${constants.BASE_URL}/api/v1/oauth/token/`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                console.log(result);
                if (result && result.access_token && result.refresh_token && result.expires_in) {
                    const encrypted_access_token = CryptoJS.AES.encrypt(result.access_token, Key).toString();
                    const encrypted_refresh_token = CryptoJS.AES.encrypt(result.refresh_token, Key).toString();
                    const encrypted_expires_in = CryptoJS.AES.encrypt(result.expires_in, Key).toString();
                    localStorage.setItem("titanfile_access", encrypted_access_token);
                    localStorage.setItem("titanfile_refresh", encrypted_refresh_token);
                    localStorage.setItem("expires_in", encrypted_expires_in);

                    callback(null, result.access_token);
                } else {
                    callback(result.error, null);
                }
            })
            .catch((error) => {
                console.error("Error refreshing token:", error);
                callback(error, null);
            });
    } else {
        const notExpireToken = "accessToken not expired yet";
        callback(notExpireToken, null);
    }
};
