/* eslint-disable no-undef */
/* eslint-disable prettier/prettier */
import constants from "../utils/constants";
import CryptoJS from "crypto-js";

export const CreateNewChannel = (accessToken, subject, channelsdata, callback) => {
    const Key = constants.secretKey;
    let encryptedOwner_email = localStorage.getItem("owner_email");
    console.log(subject);

    let decryptedOwner_email;
    if (encryptedOwner_email) {
        const bytes = CryptoJS.AES.decrypt(encryptedOwner_email, Key);
        decryptedOwner_email = bytes.toString(CryptoJS.enc.Utf8);
    } else {
        decryptedOwner_email = null;
    }

    let channelName;
    if (subject == "") {
        const matchedChannel = channelsdata.find(channel => channel.name == "New Channel");
        if (matchedChannel) {
            console.log("New Channel already exists:", matchedChannel);
            const randomNumber = Math.floor(Math.random() * 900) + 100;
            channelName = `New Channel ${randomNumber}`
        } else {
            channelName = "New Channel"
        }
    } else {
        channelName = subject;
    }

    if (accessToken && subject) {
        const myHeaders = new Headers();
        myHeaders.append("Accept", `application/json; ${constants.version}`);
        myHeaders.append("Authorization", `Bearer ${accessToken}`);
        myHeaders.append("Cookie", `sessionid=${constants.session_id}`);

        const formdata = new FormData();
        formdata.append("name", channelName);
        formdata.append("owner_email", decryptedOwner_email);

        const requestOptions = {
            method: "POST",
            headers: myHeaders,
            body: formdata,
            redirect: "follow"
        };

        fetch(`${constants.BASE_URL}/api/v1/channels/`, requestOptions)
            .then((response) => response.json())
            .then((result) => {
                const data = result;
                if (data.id) {
                    console.log(data);
                    callback(null, data);
                } else if (data.error) {
                    console.log(data.error.message);
                    callback(data.error.message, null);
                }
                else {
                    console.log("Channel not created");
                    callback("Channel not created");
                }
            })
            .catch((error) => {
                console.error(error);
                callback(error.message, null)
            });
    }
};