/* eslint-disable @typescript-eslint/no-unused-vars */
import React from 'react';
import { AppBar, Box, Toolbar, Button, Stack } from '@mui/material';
import constants from '../../../utils/constants';
import { useNavigate } from 'react-router-dom';
import CryptoJS from 'crypto-js';
import { useTranslation } from 'react-i18next';
import LanguageProvider from '../../../locales/LanguageProvider';

const Header = () => {
    const navigate = useNavigate();
    const { t, i18n } = useTranslation();
    const currentLanguage = i18n.language;

    var Useremail = localStorage.getItem("owner_email");
    const Key = constants.secretKey;

    let decryptedEmail;
    if (Useremail) {
        const bytes = CryptoJS.AES.decrypt(Useremail, Key);
        decryptedEmail = bytes.toString(CryptoJS.enc.Utf8);
    }

    const handleSignOut = () => {
        console.log("sign out function called");

        localStorage.removeItem("titanfile_access");
        localStorage.removeItem("owner_email");
        localStorage.removeItem("titanfile_refresh");

        // Navigate to the home/login page
        navigate("/")
    }

    return (
        <>
            <Box sx={{ flexGrow: 1 }}>
                <AppBar
                    sx={{
                        backgroundColor: 'white',
                        color: 'black',
                        boxShadow: '0 0 white'
                    }}
                    position="static"
                >
                    <Toolbar sx={{ minHeight: "46px", display: "flex", justifyContent: "space-between" }}>
                        <Stack>
                            <img style={{ height: "20px" }} src="../assets/titanfilelogin.svg" alt="TitanFile" />
                        </Stack>
                        <Stack sx={{ display: "flex", flexDirection: "row" }}>
                            <LanguageProvider />
                            <Button
                                sx={{
                                    color: "inherit",
                                    textTransform: "capitalize",
                                    fontSize: currentLanguage === 'fr' ? '12px' : 'small',
                                }}
                                onClick={handleSignOut}
                            >
                                {t('logout_text')}
                            </Button>
                        </Stack>
                    </Toolbar>
                </AppBar>
            </Box>
        </>
    )
}

export default Header;
