/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Box, FormControl, Select, MenuItem } from "@mui/material";

const LanguageProvider = () => {
    const { i18n } = useTranslation();
    const [language, setLanguage] = useState('English'); // Set default to English

    const handleLanguageChange = (event) => {
        const lng = event.target.value;
        setLanguage(lng);
        i18n.changeLanguage(lng === 'English' ? 'en' : 'fr');
        localStorage.setItem("selected_language", lng);
    };

    useEffect(() => {
        const selectedLanguage = localStorage.getItem("selected_language");
        if (selectedLanguage) {
            setLanguage(selectedLanguage);
            i18n.changeLanguage(selectedLanguage === 'English' ? 'en' : 'fr');
        }
    }, [i18n]);

    return (
        <Box sx={{ minWidth: 80 }}>
            <FormControl fullWidth size="small" variant="outlined">
                <Select
                    id="language-select"
                    value={language}
                    onChange={handleLanguageChange}
                    displayEmpty
                    renderValue={(selected) => selected} // Display the selected value
                    inputProps={{ 'aria-label': 'Without label' }}
                    sx={{ border: '1px solid #ced4da', borderRadius: 1 }}
                >
                    <MenuItem value="English">English</MenuItem>
                    <MenuItem value="Franais">Franais</MenuItem>
                </Select>
            </FormControl>
        </Box>
    );
}

export default LanguageProvider;
