/* eslint-disable no-undef */
/* eslint-disable react/prop-types */
/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState } from 'react';
import { Stack, Typography } from '@mui/material';
import { Token } from '../../../utils/accessToken';
import axios from 'axios';
import constants from '../../../utils/constants';
import Toast from '../Toast/Toast';

const FileUpload = ({ selectedFile, isUploading, onUploadComplete, abortController }) => {
    const [info, setInfo] = useState(false); // State to store info message
    const [error, setError] = useState(false); // State to store error message
    const [uploadedChunks, setUploadedChunks] = useState(0);
    const [dynamicFileNo, setDynamicFileNo] = useState(0); // Initialize dynamic file number
    const [uploadPercentage, setUploadPercentage] = useState(0);
    const [targetPercentage, setTargetPercentage] = useState(0); // Add target percentage state
    const [showUploadingMessage, setShowUploadingMessage] = useState(true);
    const accessToken = Token();

    let CHUNK_SIZE;
    const UPLOAD_URL = `${constants.BASE_URL}/api/v1/UploadUrl/`;
    const COMPLETE_UPLOAD_URL = `${constants.BASE_URL}/api/v1/CompleteUpload/`;

    if (selectedFile.size <= 10245760) {
        CHUNK_SIZE = selectedFile.size;
    } else {
        CHUNK_SIZE = 10485760;
    }

    const getAuthHeader = () => {
        return 'Bearer ' + accessToken;
    };

    const uploadChunk = async (chunk, chunkUploadUrl) => {
        await axios.put(chunkUploadUrl, chunk, {
            headers: {
                'Content-Type': 'application/octet-stream',
            },
            signal: abortController.signal
        });
    };

    useEffect(() => {
        if (selectedFile) {
            handleUpload();
            console.log(selectedFile);
        } else {
            setInfo("Please select a file");
        }

        // Cleanup function to abort ongoing requests if the component unmounts
        return () => {
            abortController.abort();
        };
    }, [selectedFile]);

    const handleUpload = async () => {
        if (selectedFile && selectedFile.size > 10245760) {
            try {
                const response = await axios.post(
                    UPLOAD_URL,
                    `size=${selectedFile.size}`,
                    {
                        headers: {
                            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
                            'Authorization': getAuthHeader(),
                        },
                        signal: abortController.signal
                    }
                );

                const responseData = response.data;
                console.log(responseData);

                const uploadUrl = responseData.url;
                const uploadFields = responseData.fields;
                const uploadKey = uploadFields.Key;
                const uploadBucket = uploadFields.Bucket;
                const uploadId = uploadFields.UploadId;

                const totalChunks = Math.ceil(selectedFile.size / CHUNK_SIZE);
                for (let i = 0; i < totalChunks; i++) {
                    const start = i * CHUNK_SIZE;
                    const end = Math.min(start + CHUNK_SIZE, selectedFile.size);
                    const chunk = selectedFile.slice(start, end);

                    const partResponse = await axios.post(
                        UPLOAD_URL,
                        `key=${uploadKey}&upload_id=${uploadId}&part_num=${i + 1}`,
                        {
                            headers: {
                                'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
                                'Authorization': getAuthHeader(),
                            },
                            signal: abortController.signal
                        }
                    );

                    const partData = partResponse.data;
                    const chunkUploadUrl = partData.url;

                    await uploadChunk(chunk, chunkUploadUrl);

                    const updatedChunks = i + 1;
                    setUploadedChunks(updatedChunks);
                    const newTargetPercentage = Math.round((updatedChunks / totalChunks) * 100);
                    setTargetPercentage(newTargetPercentage); // Set target percentage
                }

                const completeResponse = await axios.post(
                    COMPLETE_UPLOAD_URL,
                    `Bucket=${uploadBucket}&Key=${uploadKey}&UploadId=${uploadId}&file_name=${selectedFile.name}&size=${selectedFile.size}`,
                    {
                        headers: {
                            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
                            'Authorization': getAuthHeader(),
                        },
                        signal: abortController.signal
                    }
                );

                console.log(completeResponse.data);
                const uploadFileId = completeResponse.data.result[0].id;
                console.log(uploadFileId);

                if (uploadFileId) {
                    onUploadComplete(uploadFileId);
                    setShowUploadingMessage(false); // Hide the "Uploading File..." message
                    setDynamicFileNo((prevCount) => prevCount + 1); // Increment the dynamic file number
                    setTargetPercentage(100); // Set target percentage to 100% when upload is complete
                }
            } catch (error) {
                console.log(error);
                setError(error.message);
            }
        } else {
            setInfo("Please select a file");
        }
    }

    const animateProgress = (currentPercentage, targetPercentage) => {
        const increment = (targetPercentage - currentPercentage) / 50;
        let newPercentage = currentPercentage;

        const animationInterval = setInterval(() => {
            newPercentage += increment;
            if (newPercentage >= targetPercentage) {
                newPercentage = targetPercentage;
                clearInterval(animationInterval);
            }
            setUploadPercentage(Math.round(newPercentage));
        }, 20);
    }

    useEffect(() => {
        animateProgress(uploadPercentage, targetPercentage); // Animate towards target percentage
    }, [targetPercentage]);

    return (
        <>
            {selectedFile && (
                <Stack>
                    {showUploadingMessage && isUploading && <Typography
                        sx={{
                            color: "black",
                            paddingTop: "1.5rem",
                            textAlign: "center"
                        }}
                    >
                        Uploading File...
                    </Typography>}
                    <Typography sx={{ color: "black", paddingTop: "1.5rem", paddingBottom: "0.5rem", textAlign: "center" }}>
                        {dynamicFileNo} of {dynamicFileNo}
                    </Typography>
                    <Stack sx={{ width: "91%", marginLeft: "0.75rem", marginRight: "0.75rem" }}>
                        <Stack sx={{ width: '100%', height: '1rem', backgroundColor: '#F0F0F0', borderRadius: '0.5rem' }}>
                            <Stack
                                sx={{
                                    border: '1px solid #41AD49',
                                    borderRadius: '0.5rem',
                                    height: '1rem',
                                    background: 'linear-gradient(to right, #41AD49, #78BB33)'
                                }}
                                style={{ width: `${uploadPercentage}%` }}>
                            </Stack>
                        </Stack>
                    </Stack>
                    <Typography sx={{ color: "black", paddingTop: "0.5rem", textAlign: "center" }}>
                        {uploadPercentage}%
                    </Typography>
                </Stack>
            )}

            {/* Display error message if error state is not null else show info */}
            {error ? (
                <Toast error={error} />
            ) : info ? (
                <Toast info={info} />
            ) : null}
        </>
    );
};

export default FileUpload;
